export default {
	data() {
		return {
			isRoom:true
		}
	},
	onShow() {
		this.getToken()
	},
	onLoad:function(option){
		if(option.isRoom){
			this.isRoom = false
		}
	},
		
	methods: {
		//输入框
		Input:function(event){
			this.content = event.target.value
		},
		//获取token
		getToken(){
			var that = this
			uni.getStorage({
				key:'token',	
				success: function (res) {
				that.token = res.data.token
				},
			})
		},
		//提交反馈
		toSfeedback(){
			var that = this;
			if(this.content == undefined){
				uni.showToast({
					title:'输入内容不能为空！',
					icon:'none'
				})
			}else{
				uni.request({
					url:'http://112.74.86.165:8082/feedback',
					method:'POST',
					data:{
						"content": this.content,
						"type": this.isRoom
					},
					header:{
						Authorization:that.token
					},
					success(res) {
					console.log(res)
						uni.showToast({
							title:'提交成功',
						})
					setTimeout(function() {
					       uni.switchTab({
					       	url:'../user'
					       })
					    },1000);
					}
				})
			}
		}
	}
}
